
@keyframes load{
form{transform:rotate(0)
}
to{transform:rotate(365deg)
}
}
.loading{width:30px;height:30px;border-radius:50%;border-width:3px;border-color:#3c3c3c;border-left-color:transparent;border-style:solid;position:fixed;left:50%;top:50%;margin-top:-15px;margin-left:-15px;animation:load 1s linear infinite
}
#app.readerNightMode .loading{border-color:#ececec;border-left-color:transparent
}



