
.page-title{position:fixed;width:calc(100% - 50% - 20px)
}
.page-title li{float:left
}
.page-title li.search{float:right;margin-top:10px;width:183px;height:30px;position:relative
}
.page-title li.search input{width:100%;height:100%;background-color:transparent;border:1px solid #cccccc;box-shadow:inset 0 1px 1px rgba(0,0,0,0.075);transition:border linear 0.2s, box-shadow linear 0.2s;border-radius:15px;outline:none;box-sizing:border-box;padding:0 25px 0 10px;font-size:14px
}
.page-title li.search input:focus{border-color:#12b7f5
}
.page-title li.search i{position:absolute;right:10px;top:6px;font-size:12px;color:#999999
}
.page-title li.active{margin-bottom:-2px;border-bottom:2px solid #555555
}
.page-title li.active a{color:#555555
}
.page-title{padding-left:20px;border-bottom:2px solid #d9d9d9;background:#fff;z-index:2
}
.page-title a{color:#999;display:inline-block;padding:15px;transition:.5s ease
}
.page-title a:hover{background:#eeeeee
}



