
.left-aside{position:fixed;top:0;right:0;bottom:0;left:170px;width:25%;z-index:1
}
.left-aside .cover-img{position:absolute;top:0;left:0;width:100%;height:100%;background-position:center;background-size:cover
}
.left-aside .bottom-block{position:absolute;bottom:0;padding:30px;color:white;font-family:'宋体'
}
.left-aside .bottom-block h1{font-size:38.5px
}
.left-aside .bottom-block h3{font-size:24.5px
}
.left-aside .bottom-block h1,.left-aside .bottom-block h3{line-height:40px;text-shadow:-1px 0 0 rgba(0,0,0,0.75);margin:10px 0
}
.left-aside .bottom-block .btn-success{margin-top:10px
}
.left-aside .bottom-block .btn-success i{margin-right:5px
}



