
.navbar{position:fixed;left:0;top:0;height:100%;background:#2a2a2a;width:170px;z-index:2
}
.navbar a{padding:10px;display:block;color:#999999;line-height:25px
}
.navbar a i{display:inline-block;margin-right:10px
}
.dropdown a{text-shadow:0 1px 0 black
}
#app .dropdown a.router-link-active{text-shadow:0 1px 0 #de533a;background-color:#e78170;color:white;text-shadow:0 1px 0 #de533a
}
#app .dropdown a:hover{text-shadow:0 1px 0 #de533a;background:#f49484;color:white
}
#app .dropdown a:active{text-shadow:0 0 0 #de533a;background:#b34129;color:white
}
.nav-user{position:absolute;bottom:0;width:100%;padding-bottom:5px
}
.nav-user a{cursor:pointer
}
.nav-user a:hover{color:white
}
#view-mode-modal{left:180px;bottom:36px;width:175px;padding:10px 10px 0;background:#eeeeee;border-radius:4px;box-shadow:0 2px 8px rgba(0,0,0,0.1);border:1px solid #d9d9d9;font-size:14px;font-weight:normal;line-height:20px;position:fixed
}
.read-modal::before{position:absolute;top:77px;left:-7px;content:'';display:inline-block;border-top:7px solid transparent;border-bottom:7px solid transparent;border-right:7px solid #d9d9d9
}
.read-modal .btn-group{margin:0 0 10px 0
}
.btn-group{position:relative;font-size:0;vertical-align:middle;white-space:nowrap;overflow:hidden
}
.btn-group span{font-size:14px;display:inline-block;width:50%;text-align:center;padding:10px 0;color:#555555;background-color:#f6f6f6;border:1px solid #d4d4d4;box-shadow:inset 0 1px 0 rgba(255,255,255,0.4),0 1px 0 rgba(255,255,255,0.4);box-sizing:border-box;cursor:pointer
}
.btn-group span:first-of-type{border-right:none;border-radius:5px 0 0 5px
}
.btn-group span:last-of-type{border-radius:0 5px 5px 0
}
.btn-group span.active{background:#e9e9e9;box-shadow:inset 0 1px 1px rgba(0,0,0,0.03),0 1px 0 rgba(255,255,255,0.4)
}
.btn-group span.night.active{background:#2a2a2a;color:#fff
}
.btn-group span i{font-size:20px
}



