
.articleList{margin-left:25.0%;height:100%;padding-left:170px;width:50%
}
#list-container{padding-top:65px;padding-left:32px
}
li.list-itme{position:relative;width:100%;padding-right:2px;padding-bottom:17px;margin:0 0 17px;border-bottom:1px dashed #d9d9d9;box-sizing:border-box;word-wrap:break-word;overflow:hidden
}
li.list-itme .list-top{margin:8px 0;font-size:13px
}
li.list-itme .list-top .time{margin-left:3px;color:#717171
}
li.list-itme .title{margin-top:0;margin-bottom:10px;font-size:18px;font-weight:bold;line-height:1.5
}
li.list-itme .list-footer{font-family:-apple-system, "Helvetica Neue", Arial, "PingFang SC", "lucida grande", "lucida sans unicode", lucida, helvetica, "Hiragino Sans GB", "Microsoft YaHei", "WenQuanYi Micro Hei", sans-serif;font-size:12px;font-weight:normal;line-height:20px
}
li.list-itme .list-footer a,li.list-itme .list-footer span{color:#999999
}
li.list-itme .list-footer a:hover,li.list-itme .list-footer span:hover{color:#717171
}
li.list-itme .wrap-img{float:right;width:100px;height:100px
}
li.list-itme .wrap-img img{width:100%;height:100px;width:auto\9;border-radius:4px;border:1px solid #eeeeee;box-sizing:border-box
}
#app.readerNightMode .page-title{border-bottom:2px solid #2f2f2f;background:#3f3f3f
}
#app.readerNightMode .page-title a:hover{background:#262626;color:#b1b1b1
}
#app.readerNightMode .page-title input{background:#555555;border-color:#2f2f2f;color:#999999
}
#app.readerNightMode .sort-nav li{float:left;margin:10px 15px 5px 0
}
#app.readerNightMode .sort-nav li a{border:1px solid #2f2f2f;background:#2f2f2f;color:#b1b1b1
}
#app.readerNightMode .sort-nav li a:hover{color:#fff
}
#app.readerNightMode .sort-nav li.active a{color:white;border-color:#000;background:#000
}
#app.readerNightMode .list-itme{border-color:#2f2f2f
}
#app.readerNightMode .list-itme .wrap-img img{border-color:#2f2f2f
}
#app.readerFont .title{font-family:-apple-system, "Helvetica Neue", Arial, "PingFang SC", "lucida grande", "lucida sans unicode", lucida, helvetica, "Hiragino Sans GB", "Microsoft YaHei", "WenQuanYi Micro Hei", sans-serif
}



