
.articleCon{width:620px;margin:0 auto;padding:80px 40px 20px
}
.articleCon .author-info{padding-bottom:15px;margin-bottom:30px;border-bottom:1px solid #eeeeee;overflow:hidden;font-size:12px;color:#999999
}
.articleCon .author-info img{width:100%;height:100%;border:2px solid white;border-radius:50%;box-sizing:border-box
}
.articleCon .author-info .avatar{float:left;width:40px;height:40px;margin-right:10px
}
.articleCon .author-info .label{background:none;border:1px solid #e78170;color:#e78170;text-shadow:none;font-size:11px;font-weight:normal;line-height:1;border-radius:3px;display:inline-block;padding:2px 4px
}
.articleCon .author-info .author-name{margin:0 5px
}
.articleCon .author-info .text{margin-top:5px
}
.articleCon .title{word-break:break-all;font-size:32px;font-weight:bold;line-height:1.5;font-family:Georgia, "Times New Roman", Times, "Kai", "Kaiti SC", "KaiTi", "BiauKai", "楷体", "楷体_GB2312", serif;color:#2a2a2a;margin-bottom:15px
}
#app.readerNightMode .articleCon .title{color:#e8e8e8
}



